<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '../dashboard' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    <div class="main-panle">
      <form class="from-reset relative" :model="formInline">
            <div class="from-group" style="width:150px">
              <el-input
                v-model="formInline.order_id"
                placeholder="">
              </el-input>
            </div>
            <div class="from-group form_phone">
              <el-input
                v-model="formInline.phone"
                placeholder="">
              </el-input>
            </div>
            <div class="from-group" style="width:110px">
              <el-select v-model="formInline.status" placeholder="">
                  <el-option label="" :value="8"></el-option>
                  <el-option label="" :value="2"></el-option>
                  <el-option label="" :value="3"></el-option>
                  <el-option label="" :value="4"></el-option>
                  <el-option label="" :value="5"></el-option>
                  <el-option label="" :value="6"></el-option>
                  <el-option label="" :value="7"></el-option>
              </el-select>
            </div>
            <div class="from-group" style="width:150px">
              <el-date-picker
              width="80"
              v-model="formInline.create_date"
              default-time="00:00:00"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="">
            </el-date-picker>
            </div>
            <div class="from-group">
              <el-button type="primary" @click="getList(true)"></el-button>
            </div>
              <div class="from-group">
              <el-button type="primary" @click="add('ruleForm')"></el-button>
            </div>
        </form>
      </div>
      <el-table
        v-loading="loading"
        :data="tableData"
          border
          style="width: 100%">
          <el-table-column
          width="150"
          label=""
          align='center'>
          <template slot-scope="scope">
            <p class="dtail_number" @click='detail(scope.row.order_id)'>{{scope.row.order_id}}</p>
          </template>
          </el-table-column>
          <el-table-column
          label=""
          prop="phone"
          align='center'>
          </el-table-column>
          <el-table-column
          align='center'
          prop="total_fee"
          label="/">
          </el-table-column>
          <el-table-column
          align='center'
          prop="create_date"
          label="">
          </el-table-column>
          <el-table-column
            align='center'
            label="">
            <template slot-scope="scope">
              <span v-if="!!scope.row.invoiceurl"></span>
              <span v-else></span>
              <!-- <img v-if="!!scope.row.invoiceurl" :src="scope.row.invoiceurl" width="150" height="100">
              <template v-else></template> -->
            </template>
          </el-table-column>
          <el-table-column
          align='center'
          label="">
            <template slot-scope="scope">
                <p v-if="scope.row.status == 0"></p>
                <p v-else-if="scope.row.status == 1"></p>
                <p v-else-if="scope.row.status == 2"></p>
                <p class="enable" v-else-if="scope.row.status == 3"></p>
                <p class="disable" v-else-if="scope.row.status == 4"></p>
                <p class="orange" v-else-if="scope.row.status == 5"></p>
                <p class="disable" v-else-if="scope.row.status == 6"></p>
                <p class="disable" v-else-if="scope.row.status == 7"></p>
            </template>
          </el-table-column>
          <el-table-column
          align='center'
          width="220"
          label="">
          <template slot-scope="scope">
            <el-dropdown @command="handleCommand(scope.row.order_id,$event,scope.row.type)" class="el_more">
                <span class="el-dropdown-link">
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <div v-if="scope.row.status === 1">
                    <el-dropdown-item command="a"></el-dropdown-item>
                  </div>
                  <div v-if="scope.row.status === 2">
                    <el-dropdown-item command="c"></el-dropdown-item>
                    <el-dropdown-item command="d"></el-dropdown-item>
                    <el-dropdown-item command="e"></el-dropdown-item>
                  </div>
                  <div v-if="scope.row.status === 3">
                    <el-dropdown-item command="f"></el-dropdown-item>
                  </div>
                  <div v-if="scope.row.status === 4">
                    <el-dropdown-item command="h"></el-dropdown-item>
                  </div>
                  <div v-if="scope.row.status === 5">
                    <el-dropdown-item command="f"></el-dropdown-item>
                  </div>
                  <div v-if="scope.row.status === 6">
                    <el-dropdown-item command="g"></el-dropdown-item>
                  </div>
                </el-dropdown-menu>
              </el-dropdown>
            <button type="button" class="el-button el-button--danger el-button--small" @click="deleted(scope.row.order_id)"><span></span></button>
          </template>
        </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
        </el-table>
        <!--  -->
        <!-- :close-on-click-modal="false"
        :close-on-press-escape="false" -->
        <el-dialog
        :title="addTableData.aid==='-1'?'':''" v-model="addTableData.id"
        :visible.sync="dialogVisible"
        width="50%">
        <el-form :model="addTableData" label-width="100px" class="demo-ruleForm">
          <el-form-item label="">
            <el-input v-model="addTableData.invoiceurl " style="width:70%" placeholder="" readonly="readonly"></el-input>
            <el-upload class="upload-demo fr"
                action="123"
                :before-upload="beforeUpload"
                ref="newupload"
                :auto-upload="true">
                <el-button slot="trigger" size="small" type="primary"  @click="newSubmitForm()"></el-button>
              </el-upload>
          </el-form-item>
          <el-form-item label="" maxlength="11">
            <el-input v-model="addTableData.phone" placeholder="" style="width:160px" maxlength="11"></el-input>
            <el-button size="small" class="surephone" @click="surenumber"></el-button>
          </el-form-item>
          <!--  -->
          <el-table
          v-show="numberphone"
          :data="tableDataone"
          style="width: 100%">
          <el-table-column
            label="">
            <template slot-scope="scope">
                <div slot="reference" class="name-wrapper">
                  <el-tag size="medium">{{ scope.row.name }}</el-tag>
                </div>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            prop="phone">
          </el-table-column>
          <el-table-column
            label=""
            prop="address">
            <!-- <template slot-scope="scope">
                <Button type="info"></Button>
            </template> -->
          </el-table-column>
        </el-table>
        <el-form-item v-show="numberphone" label="" maxlength="11" class="busines">
          <el-input v-model="addTableData.namephone" placeholder="" style="width:160px" maxlength="11"></el-input>
          <el-button size="small" class="surephone" @click="goodsumber"></el-button>
        </el-form-item>
        <!--  -->
          <el-table
          v-show="goodshow"
          :data="tableDatatwo"
          style="width: 100%">
          <el-table-column
            label="">
            <template slot-scope="scope">
                <div slot="reference" class="name-wrapper">
                  <el-tag size="medium">{{ scope.row.name }}</el-tag>
                </div>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            prop="phone">
          </el-table-column>
          <el-table-column
            label=""
            prop="address">
          </el-table-column>
        </el-table>
        <!--  -->
          <form class="from-reset relative f_seach d_seach" :model="formInline" v-show="goodshow">
            <div class="from-group form_dugr">
              <el-input
                v-model="formInline.name"
                placeholder="">
              </el-input>
            </div>
            <div class="from-group">
              <el-button type="primary" @click="drugseach(true)"></el-button>
            </div>
          </form>
          <el-table
          v-show="goodshow"
          ref="multipleTable"
          :data="tableData3"
          tooltip-effect="dark"
          style="width: 100%">
          <el-table-column
            prop="name"
            label="">
          </el-table-column>
          <el-table-column
            prop="price"
            label=""
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="num"
            label=""
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            label=""
            show-overflow-tooltip>
            <template slot-scope="scope">
              <span class="red">{{scope.row.totalmoney}}</span>
            </template>
          </el-table-column>
          <el-table-column
            width="180"
            label="">
            <template slot-scope="scope">
              <el-input-number v-model="scope.row.num1" @change="handlenumber(scope.row, $event)" :min="0"></el-input-number>
            </template>
          </el-table-column>
        </el-table>
        <div class="detail_page" v-show="goodshow">
          <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange1"
              :current-page="currentPage1"
              :page-sizes="pageSizes"
              :page-size="pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="totalpe">
          </el-pagination>
        </div>
        <el-form-item label="" v-show="goodshow"><span class="totles">{{totals}}/</span></el-input></el-form-item>
        <el-form-item label="" v-show="goodshow">
            <el-input v-model="addTableData.discount" placeholder="" style="width:160px" maxlength="11"></el-input>
        </el-form-item>
        <el-form-item label="" v-show="goodshow">
          <el-input v-model="addTableData.receipts" placeholder="" style="width:160px" maxlength="11"></el-input>
        </el-form-item>
        <el-form-item label="" v-show="goodshow">
          <el-input
            type="textarea"
            autosize
            placeholder=""
            v-model="addTableData.remarks">
          </el-input>
        </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-cons">
          <el-button @click="cancelForm('ruleForm')"> </el-button>
          <el-button type="primary" @click="submitForm('ruleForm')" :loading='sureLoading'> </el-button>
        </span>
      </el-dialog>
      <!--  -->
        <el-dialog
        :title="''"
        :visible.sync="dialogpayment"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        width="35%">
        <el-form :model="paymentTableData" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="">
            <el-input
              type="textarea"
              autosize
              placeholder=""
              v-model="paymentTableData.remark">
            </el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="cancelForm('ruleForm')"> </el-button>
          <el-button type="primary" @click="submitpayment('ruleForm')" :loading='sureLoading'> </el-button>
        </span>
      </el-dialog>
<!--  -->
        <el-dialog
        :title="''"
        :visible.sync="dialogdetail"
        width="45%">
        <el-table
        v-loading="loading"
        :data="tableDatadetail"
          border
          style="width: 100%;margin-top:15px">
          <el-table-column
          align='center'
          prop="company"
          label="">
          </el-table-column>
          <el-table-column
          align='center'
          prop="name"
          label="">
          </el-table-column>
          <el-table-column
          align='center'
          prop="num"
          label="">
          </el-table-column>
          <el-table-column
          align='center'
          prop="trueprice"
          label="/">
          </el-table-column>
          <el-table-column
          align='center'
          prop="totalprice"
          label="/">
          </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange2"
            @current-change="handleCurrentChange2"
            :current-page="currentPage2"
            :page-sizes="pageSizes2"
            :page-size="pagesize2"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totaldetail">
        </el-pagination>
        <div class="bre">
          <h3>:</h3>
          <p class="detail_remark">{{remarkss}}</p>
        </div>
        <div>
          <h3>:<span class="totles">{{totalfee}}/</span></h3>
        </div>
      </el-dialog>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  data () {
    return {
      // 
      currentPage: 1,
      currentPage1: 1,
      currentPage2: 1,
      pageSizes: [10],
      pagesize: 10,
      pageSizes2: [10],
      pagesize2: 10,
      tableData: [],
      tableDatadetail: [],
      tableDatatwo: [],
      totalpe: 0,
      total: 0,
      totaldetail: 0,
      loading: true,
      sureLoading: false,
      numberphone: false,
      shoplist: false,
      goodshow: false,
      classA: false,
      classB: true,
      aid: '',
      imageUrl: '',
      remarkss: '',
      totalfee: '',
      // 
      formInline: {
        order_id: '',
        phone: '',
        status: '',
        create_date: ''
      },
      dialogVisible: false,
      dialogdetail: false,
      dialogpayment: false,
      addTableData: {
        namephone: '',
        phone: '',
        name: '',
        aid: '',
        stock: '',
        company: '',
        specifications: '',
        price: '',
        sn: '',
        period: '',
        enddate: '',
        sta_goods: 1,
        status: 1,
        remarks: '',
        discount: '',
        receipts: '',
        tag: '',
        invoiceurl: '',
        totals: '0',
      },
      paymentTableData: {
        remark: ''
      },
      tableDataone: [],
      tableData3: [],
      num: 1,
      pp: '',
      va: '',
      totalmoney: '',
      arrayFilter: [],
      totals: '0'
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    beforeUpload (file) {
      let da = new FormData()
      da.append('image', file)
      da.append('dic', 'invoice')
      this.$axios.post(domain.testUrl + '/admin/upload/index', da, {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.addTableData.invoiceurl = da.data
          this.$Notice.success({title: da.message})
        } else {
          this.loading = false
          this.$Notice.warning({title: da.message})
        }
      })
      return true
    },
    detail (id) {
      let d = {
        tag: 'order',
        op: 'getone',
        condition: {
          'id': id
        },
        data: {
          status: 1
        }
      }
      var pa = this.currentPage2
      this.dialogdetail = true
      this.$axios.post(domain.testUrl + '/admin/order/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDatadetail = da.data.goodsinfo.list
          this.totaldetail = da.data.goodsinfo.total
          this.remarkss = da.data.remarkss
          this.totalfee = da.data.total_fee
        } else {
          this.loading = false
        }
      })
    },
    handleCommand (id, command, type) {
      let _this = this
      this.pointid = id
      _this.dialogpayment = true
      if (command === 'a') {
        this.state = '2'
      } else if (command === 'c') {
        this.state = '3'
      } else if (command === 'd') {
        this.state = '4'
      } else if (command === 'e') {
        this.state = '5'
      } else if (command === 'f') {
        this.state = '6'
      } else if (command === 'g') {
        this.state = '7'
      }
    },
    // 
    handlenumber (t, value) {
      var totalPrice = 0
      t.totalmoney = Math.floor((t.price * value) * 100) / 100
      var arry = this.tableData3.map(user => {
        let {id, num1, price,totalmoney} = user
        let num = user.num1
        return {id, num, price,totalmoney}
      })
      this.arrayFilter = arry.filter(item => {
        return item.num !== 0
      })
      this.arrayFilter.forEach(function (val,index) {
        totalPrice += val.num*val.price;//
      });
      this.totals =parseFloat(totalPrice);
    },

    // 
    surenumber () {
      if (this.addTableData.phone === '' || this.addTableData.phone === null || this.addTableData.phone === undefined) {
        this.$Notice.warning({title: ''})
        return
      }
      let d = {
        tag: 'member',
        op: 'getlist',
        condition: {
          phone: this.addTableData.phone
        }
      }
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDataone = da.data.list
          this.numberphone = true
          this.recoverid = da.data.list[0].id
        } else {
          this.numberphone = false
          this.$Notice.warning({title: da.message})
        }
      })
    },
    drugseach (isSearch) {
      if (isSearch) {
        this.currentPage = 1
      }
      let d = {
        tag: 'member',
        op: 'getmemberproduct',
        condition: {
          id: this.listid
        }
      }
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData3 = da.data.list
          this.tableData3.map((v, i) => {
            this.$set(v, 'num1', 0)
            this.$set(v, 'totalmoney', 0)
            v.final = v.default + v.num1
          })
          this.totalpe = da.data.total
          this.loading = false
        } else {
          this.loading = false
          this.tableData3 = []
          this.totalpe = 0
          this.$Notice.warning({title: da.message})
        }
      })
    },
    // 
    goodsumber () {
      if (this.addTableData.namephone === '' || this.addTableData.namephone === null || this.addTableData.namephone === undefined) {
        this.$Notice.warning({title: ''})
        return
      }
      let d = {
        tag: 'member',
        op: 'getlist',
        condition: {
          phone: this.addTableData.namephone
        }
      }
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDatatwo = da.data.list
          this.listid = da.data.list[0].id
          this.goodshow = true
          this.drugseach()
        } else {
          this.goodshow = false
          this.$Notice.warning({title: da.message})
        }
      })
    },
    add (formName) {
      this.imageUrl = ''
      this.dialogVisible = true
      this.goodshow = false
      this.numberphone = false
      this.addTableData = {
        name: '',
        aid: '-1',
        stock: '',
        company: '',
        specifications: '',
        price: '',
        sn: '',
        period: '',
        enddate: '',
        sta_goods: 1,
        status: 1,
        discount: '',
        receipts: '',
        tag: '',
        num: 1,
        invoiceurl: '',
        totals: '0',
        remarks: '',
        discount: '',
        receipts: ''
      }
      this.tableDatatwo = []
    },
    handleSizeChange (size) {
      this.pagesize = size
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    handleCurrentChange1 (currentPage1) {
      this.currentPage1 = currentPage1
      this.getList()
    },
    handleSizeChange2 (size) {
      this.pagesize2 = size
    },
    handleCurrentChange2 (currentPage2) {
      this.currentPage2 = currentPage2
      this.getList()
    },
    getList (isSearch) {
      let _this = this
      if (isSearch) {
        this.currentPage = 1
      }
      var pa = this.currentPage
      let d = {
        tag: 'order',
        op: 'getlist',
        condition: {
          'order_id': this.formInline.order_id,
          'phone': this.formInline.phone,
          'status': '',
          'create_date': this.formInline.create_date
        }
      }
      if (this.formInline.status === 5) {
        d.condition.status = ''
      } else {
        d.condition.status = this.formInline.status
      }
      this.$axios.post(domain.testUrl + '/admin/order/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
          this.loading = false
        } else {
          this.tableData = []
          this.loading = false
        }
      }).catch(function () {
        _this.$Notice.error({title: ''})
      })
    },
    cancelForm (formName) {
      this.dialogVisible = false
      this.dialogpayment = false
      this.$refs[formName].resetFields()
    },
    submitForm (formName) {
      let _this = this
      this.sureLoading = true
      let da = {
        tag: 'order',
        op: 'add',
        data: {
          member_id: _this.recoverid,
          to_id: _this.listid,
          invoiceurl: domain.testUrl + _this.addTableData.invoiceurl,
          remarks: _this.addTableData.remarks,
          discount: _this.addTableData.discount,
          receipts: _this.addTableData.receipts,
          goods: _this.arrayFilter
        }
      }
      this.$axios.post(domain.testUrl + '/admin/order/index', qs.stringify(da), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.getList()
          this.dialogVisible = false
          this.sureLoading = false
          _this.$Notice.success({title: da.message})
        } else {
          this.sureLoading = false
          _this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: ''})
      })
    },
    submitpayment () {
      let _this = this
      let d = {
        tag: 'order',
        op: 'state',
        condition: {
          id: this.pointid
        },
        data: {
          status: this.state,
          remark: this.paymentTableData.remark
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/order/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          this.dialogpayment = false
          _this.$Notice.success({title: da.message})
        } else {
          _this.$Notice.warning({title: da.message})
        }
      })
    },
    // 
    deleted (id) {
      let _this = this
      _this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let d = {
          tag: 'order',
          op: 'state',
          condition: {
            id: id
          },
          data: {
            status: '0',
            remark: ''
          }
        }
        _this.$axios.post(domain.testUrl + '/admin/order/index', qs.stringify(d), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(res => {
          let da = res.data
          if (da.err > 0) {
            _this.getList()
            _this.$Notice.success({title: da.message})
          } else {
            _this.$Notice.warning({title: da.message})
          }
        })
      })
    },
    changeState (id, status) {
      let _this = this
      if (status === 1) {
        status = 2
      } else {
        status = 1
      }
      let d = {
        tag: 'product',
        op: 'state',
        condition: {
          id: id
        },
        data: {
          status: status
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$message({
            type: 'success',
            message: da.message
          })
        } else {
          _this.$notify({
            title: '',
            message: da.message,
            type: 'warning'
          })
        }
      }).catch(function () {
        _this.$Notice.error({title: ''})
      })
    }
  }
}
</script>



