<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '../dashboard' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <div class="prize_content">
            <form :model="addTableData">
              <div class="main_table">
                <h2 class="main-flex"></h2>
                <el-button class="main-flex add_prize" type="primary" @click="add('ruleForm')"></el-button>
              </div>
              <ul>
                <li class="prize_name over" v-for="item in arry">
                  <div class="fl prize"><label for="prize"></label><input type="text" id="prize" v-model="item.prize" placeholder=""></div>
                  <div class="fl prize"><label for="probab"></label><input type="text" id="probab" v-model="item.v" placeholder=""></div>
                  <Icon class="close" @click="detal" type="md-close" />
                </li>
              </ul>
              <h2 class="prize_count"></h2>
              <ul>
                <li class="prize_name over">
                  <div class="fl prize"><label for="prize"></label><input type="text" id="prize" v-model="addTableData.minlevel" placeholder=""></div>
                  <div class="fl prize"><label for="probab"></label><input type="text" id="probab" v-model="addTableData.maxlevel" placeholder=""></div>
                  <div class="fl prize"><label for="probab"></label><input type="text" id="probab" v-model="addTableData.times" placeholder=""></div>
                </li>
              </ul>
            </form>
        <div slot="footer" class="dialog-footer tc">
          <el-button type="primary" @click="submitForm()" :loading='sureLoading'> </el-button>
        </div>
      </div>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  data () {
    return {
      selectData: [],
      tableData: [],
      addManagerVisible: false,
      imageUrl: '',
      state: 1,
      sureLoading: false,
      addTableData: {
        prize: '',
        v: '',
        minlevel: '',
        maxlevel: '',
        times: ''
      },
      arry:[{
        prize: '',
        v: ''
      }]
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    add () {
      this.addManagerVisible = true
      let cope = {
        prize: '',
        v: ''
      }
      this.arry.push(cope)
    },
    detal (val) {
      console.log(val)
      for (let index = 0; index < val.length; index++) {
        let vv = val
        val.forEach((val, index) => {
            this.arry.forEach((v, i) => {
              if (val.rowNum === v.rowNum) {
                // i 
                this.arry.splice(i, 1)
              }
            })
          })
      }
    },
    getList () {
      let d = {
        tag: 'banner_config',
        op: 'get'
      }
      this.$axios.post(domain.testUrl + '/admin/systemsets/banner_or_ad_set', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
        } else {
          this.loading = false
        }
      }).catch(function () {
        this.$Notice.error({title: ''})
      })
    },
    submitForm () {
      console.log(this.arry.prize)
      // let _this = this
      // this.sureLoading = true
      // let da = {
      //   data: {
      //     rules:[{
      //       prize: this.arry.prize,
      //       min: '90',
      //       max: '180',
      //       v: this.arry.v
      //     }],
      //     times: [{
      //       minlevel: '',
      //       maxlevel: '',
      //       times: ''
      //     }]
      //   }
      // }
      // this.$axios.post(domain.testUrl + '/admin/Systemsets/active_set', qs.stringify(da), {
      //   headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      // }).then(res => {
      //   let da = res.data
      //   if (da.err > 0) {
      //     this.getList()
      //     this.sureLoading = false
      //     this.addManagerVisible = false
      //     _this.$Notice.success({title: da.message})
      //   } else {
      //     this.sureLoading = false
      //     _this.$Notice.warning({title: da.message})
      //   }
      // }).catch(function () {
      //   _this.$Notice.error({title: ''})
      // })
    }
  }
}
</script>
<style>
